<template>
    <div>
        <div class="detail-header">
            <router-link tag="a" :to="{name:'cart'}">
                <div class="detail-point"></div>
            </router-link>
            <div class="detail-name">商品详情</div>

            <div class="detail-share"></div>

        </div>
        <div style="height: 0.9rem;"></div>
        <!-- 密码输入框 -->
        <van-password-input
                :value="value"
                info="密码为 6 位数字"
                :focused="showKeyboard"
                @focus="showKeyboard = true"
        />

        <!-- 数字键盘 -->
        <van-number-keyboard
                :show="showKeyboard"
                @input="onInput"
                @delete="onDelete"
                @blur="showKeyboard = false"
        />
    </div>
</template>

<script>
    import Vue from 'vue';
    import {PasswordInput, NumberKeyboard} from 'vant';
    import {payOrders} from "../../api/orders";
    import {Toast} from 'vant';
    Vue.use(Toast);

    Vue.use(PasswordInput).use(NumberKeyboard);
    export default {
        name: "Pay",
        data() {
            return {
                value: '',
                showKeyboard: true
            };
        },

        methods: {
            onInput(key) {
                this.value = (this.value + key).slice(0, 6);
            },
            onDelete() {
                this.value = this.value.slice(0, this.value.length - 1);
            },
            payOrder() {
                let {ordernum} = this.$router.query;
                payOrders('/api/orders/' + ordernum, {'status': 1}).then(res => {
                    console.log(res);
                })
            }
        },
        watch: {
            value() {
                if (this.value == '123456') {
                    this.payOrder;
                }
                if(this.value.length == 6 && !(this.value == '123456')){
                    setTimeout(()=> {
                        this.value = '';
                        Toast('请输入正确的密码');
                    },200)
                }
            }
        }
    }
</script>

<style scoped>

    .detail-header {
        position: fixed;
        left: 0;
        top: 0;
        z-index: 20;
        box-sizing: border-box;
        padding: 0 3%;
        width: 100%;
        height: 0.9rem;
        display: flex;
        justify-content: space-between;
        background: #fff;
    }

    .detail-point {
        width: 0.8rem;
        height: 0.9rem;
        background: url(../../style/img/detail/arrow-left.png) no-repeat left center;
        background-size: 60%;
    }

    .detail-name {
        margin: 0 0.8rem;
        height: 100%;
        line-height: 0.9rem;
        font-size: 0.3rem;
        text-align: center;
        font-family: 'helvetica neue', tahoma, 'hiragino sans gb', stheiti, 'wenquanyi micro hei', \5FAE\8F6F\96C5\9ED1, \5B8B\4F53, sans-serif;
    }

    .detail-share {
        width: 0.8rem;
        height: 0.9rem;
        background-size: 50%;
    }
</style>